import React from 'react';
import './MainContent.css';

import HomeContent from './Home/HomeContent';
import ContactForm from './Contact/ContactForm';
import InterestCard from './Interests/InterestCard'
import ProjectCard from './Projects/ProjectCard'
import TopicSelector from './TopicSelector/TopicSelector'



const MainContent = ({activeCategory, activeTopic, changeCategory, changeTopic}) => {

    let getContent = () => {

      let topic = activeCategory.links.find( topic =>{
        return topic.value == activeTopic;
      })

      if (topic == undefined) {
        console.log("Topic Data Not Found");
        console.log(activeCategory.links);
        console.log(activeTopic);
        return (<p>Oops... Something Went Wrong!</p>)
      }

      let topicData = topic.data;

      switch (topic.type) {
        case 'homeDefault':
          return <HomeContent onContactClick={changeCategory}/>
        case 'topic-selector':
          return <TopicSelector Topics={topicData} click={changeTopic}/>
        case 'interestDouble':
        return(
          <div className="flex flex-column items-center justify-around">
            <div className="interestDouble-top h-50 flex justify-center">
              <InterestCard imageURL={topicData[0].Image} title={topicData[0].Title}alt="photo1" content={topicData[0].Content} icon={topicData[0].icon} link={topicData[0].Link}/>
            </div>
            <div className="interestDouble-bottom h-50 flex justify-center">
              <InterestCard imageURL={topicData[1].Image} alt="photo2" title={topicData[1].Title} content={topicData[1].Content} reverse={true} icon={topicData[1].icon} link={topicData[1].Link}/>
            </div>
          </div>
        )
        case 'interestsDefault':
          return <InterestCard imageURL={topicData.Image} title={topicData.Title} content={topicData.Content} icon={topicData.icon} link={topicData.Link}/>
        case 'projectsDefault':
          return <ProjectCard imageURL={topicData.Image} title={topicData.Title} content={topicData.Content} icon={topicData.icon} link={topicData.Link}/>
        case 'contactDefault':
          return <ContactForm/>
        case 'policyDefault':
          return(
            <div className="Policy f6 pl3">
            <h1>Privacy Policy<br />Effective date: January 01, 2019</h1>
              <p>Chris Dirks ("I", "we", or "our") operates the active website: https://www.chrisdirks.com (the "Service").</p>
              <p>This page informs you of our policies regarding the collection, use, and disclosure of personal data when you use our Service and the choices you have associated with that data.</p>
              <p>We use your data to provide and improve the Service. By using the Service, you agree to the collection and use of information in accordance with this policy. <br /><br />Information Collection And Use<br />We collect several different types of information for various purposes to provide and improve our Service to you.</p>
              <h3>Types of Data Collected</h3>
              <h4>Personal Data</h4>
              <p>While using our Service, we may ask you to provide us with certain personally identifiable information that can be used to contact or identify you ("Personal Data"). Personally identifiable information may include, but is not limited to:</p>
              <ul>
              <li>Email address</li>
              <li>First name and last name</li>
              <li>Cookies and Usage Data</li>
              </ul>
              <h4>Usage Data</h4>
              <p>We may also collect information how the Service is accessed and used ("Usage Data"). This Usage Data may include information such as your computer's Internet Protocol address (e.g. IP address), browser type, browser version, the pages of our Service that you visit, the time and date of your visit, the time spent on those pages, unique device identifiers and other diagnostic data.</p>
              <h4>Tracking &amp; Cookies Data</h4>
              <p>We use cookies and similar tracking technologies to track the activity on our Service and hold certain information.</p>
              <p>Cookies are files with small amount of data which may include an anonymous unique identifier. Cookies are sent to your browser from a website and stored on your device. Tracking technologies also used are beacons, tags, and scripts to collect and track information and to improve and analyze our Service.</p>
              <p>You can instruct your browser to refuse all cookies or to indicate when a cookie is being sent. However, if you do not accept cookies, you may not be able to use some portions of our Service.</p>
              <p>Examples of Cookies we use:</p>
              <ul>
              <li><strong>Session Cookies.</strong> We use Session Cookies to operate our Service.</li>
              <li><strong>Preference Cookies.</strong> We use Preference Cookies to remember your preferences and various settings.</li>
              <li><strong>Security Cookies.</strong> We use Security Cookies for security purposes.</li>
              </ul>
              <h2>Use of Data</h2>
              <p>Chris Dirks and https://www.chrisdirks.com uses the collected data for various purposes:</p>
              <ul>
              <li>To provide and maintain the Service</li>
              <li>To notify you about changes to our Service</li>
              <li>To allow you to participate in interactive features of our Service when you choose to do so</li>
              <li>To provide customer care and support</li>
              <li>To provide analysis or valuable information so that we can improve the Service</li>
              <li>To monitor the usage of the Service</li>
              <li>To detect, prevent and address technical issues</li>
              </ul>
              <h2>Transfer Of Data</h2>
              <p>Your information, including Personal Data, may be transferred to &mdash; and maintained on &mdash; computers located outside of your state, province, country or other governmental jurisdiction where the data protection laws may differ than those from your jurisdiction.</p>
              <p>If you are located outside United States and choose to provide information to us, please note that we transfer the data, including Personal Data, to the United States and process it there.</p>
              <p>Your consent to this Privacy Policy followed by your submission of such information represents your agreement to that transfer.</p>
              <p>Chris Dirks and https://www.chrisdirks.com will take all steps reasonably necessary to ensure that your data is treated securely and in accordance with this Privacy Policy and no transfer of your Personal Data will take place to an organization or a country unless there are adequate controls in place including the security of your data and other personal information.</p>
              <h2>Disclosure Of Data</h2>
              <h3>Legal Requirements</h3>
              <p>Chris Dirks and https://www.chrisdirks.com may disclose your Personal Data in the good faith belief that such action is necessary to:</p>
              <ul>
              <li>To comply with a legal obligation</li>
              <li>To protect the rights or property of Chris Dirks and https://chrisdirks.com</li>
              <li>To prevent or investigate possible wrongdoing in connection with the Service</li>
              <li>To protect the personal safety of users of the Service or the public</li>
              <li>To protect against legal liability</li>
              </ul>
              <h2>Security Of Data</h2>
              <p>The security of your data is important to us, but remember that no method of transmission over the Internet, or method of electronic storage is 100% secure. While we strive to use commercially acceptable means to protect your Personal Data, we cannot guarantee its absolute security.</p>
              <h2>Service Providers</h2>
              <p>We may employ third party companies and individuals to facilitate our Service ("Service Providers"), to provide the Service on our behalf, to perform Service-related services or to assist us in analyzing how our Service is used.</p>
              <p>These third parties have access to your Personal Data only to perform these tasks on our behalf and are obligated not to disclose or use it for any other purpose.</p>
              <h2>Links To Other Sites</h2>
              <p>Our Service may contain links to other sites that are not operated by us. If you click on a third party link, you will be directed to that third party's site. We strongly advise you to review the Privacy Policy of every site you visit.</p>
              <p>We have no control over and assume no responsibility for the content, privacy policies or practices of any third party sites or services.</p>
              <h2>Children's Privacy</h2>
              <p>Our Service does not address anyone under the age of 18 ("Children").</p>
              <p>We do not knowingly collect personally identifiable information from anyone under the age of 18. If you are a parent or guardian and you are aware that your Children has provided us with Personal Data, please contact us. If we become aware that we have collected Personal Data from children without verification of parental consent, we take steps to remove that information from our servers.</p>
              <h2>Changes To This Privacy Policy</h2>
              <p>We may update our Privacy Policy from time to time. We will notify you of any changes by posting the new Privacy Policy on this page.</p>
              <p>We will let you know via email and/or a prominent notice on our Service, prior to the change becoming effective and update the "effective date" at the top of this Privacy Policy.</p>
              <p>You are advised to review this Privacy Policy periodically for any changes. Changes to this Privacy Policy are effective when they are posted on this page.</p>
              <h2>Contact Us</h2>
              <p>If you have any questions about this Privacy Policy, please contact us:</p>
              <ul>
              <li>By email: <a href="mailto:chrisdirks.developer@gmail.com">chrisdirks.developer@gmail.com</a></li>
              <li>By website: Through the contact form on the Contact page of this website</li>
              </ul>
              <p><a href="https://html-css-js.com/" target="_blank">HTML CSS and JavaScript</a> are the client-side elements of each website. Learn them to become a web developer.</p>
            </div>
          )    
        default:
          console.log("Topic Type not known", topic.type);
          return (<p>Oops... Something Went Wrong!</p>)
      }
    }

    return(
      <div className="MainContent flex">
        {getContent() }
      </div>
  
    )
}

export default MainContent;